body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes {
  z-index: 2;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite clock-move;
  position: absolute;
  top: calc(50% - 20vmin);
  left: calc(50% - 1.5vmin);
}

.stopwatch__seconds {
  z-index: 3;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite clock-move;
  position: absolute;
  top: calc(50% - 38vmin);
  left: calc(50% - .75vmin);
}

.stopwatch__axial {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up > .stopwatch__minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up > .stopwatch__seconds {
  animation-duration: 10s;
}

@keyframes clock-move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
